.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.title {
    position: absolute;
    left: 40vw;
    display: block;
    font-weight: bolder;
}

.loader {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.movies {
    display: grid;
    grid-template-columns: repeat(4, minmax(200px, 1fr));
    grid-gap: 30px;
    padding: 50px;
    width: 80%;
    padding-top: 70px;
}

@media screen and (max-width: 100px) {
    .movies {
        grid-template-columns: 1fr;
        width: 100%;
    }
}